.banner {
  position: fixed;
  margin: auto;
  top: 10%;
  left: 0;
  right: 0;
  background-color: #efefef;
  z-index: 1;
  height: 50px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.banner .note {
  font-size: 0.8em;
  text-align: center;
}

@media screen and (max-width: 800px) {
  .banner {
    display: none;
    position: absolute;
    height: fit-content;
    bottom: 40px;
    top: unset;
    background-color: black;
    color: white;
    padding: 10px;
    margin: 10px;
    font-size: 13px;
  }

  .banner.show {
    display: block;
  }

  .banner .note {
    text-align: left;
  }
}
